@use "global/variables" as v;
@use "global/mixins" as m;
@use "sass:color";
.overlayBody {
  position: fixed; // fijo en toda la pantalla
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(v.$Dark, 0.95); // fondo semi-transparente
  color: v.$White;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1000; // encima de todo
  padding: 0 5vw; // margen lateral (5% a cada lado)

  // contenedor interno
  .overlayBody__content {
    margin-top: 4rem;
    background-color: v.$Dark;
    border-radius: 1rem;
    padding: 2rem;
    max-width: 900px;
    width: 100%;
    text-align: center;
    box-shadow: 0 0 20px rgba(v.$White, 0.1);

    .overlayBody__nombre {
      font-size: 4rem;
      font-weight: 200;
      margin-bottom: 1rem;
    }

    .overlayBody__informacion {
      p {
        font-size: 1rem;
        line-height: 1.5;
      }
    }

    // botón cerrar
    .overlayBody__close {
      position: absolute;
      top: 2rem;
      right: 2rem;
      background: none;
      border: none;
      color: v.$White;
      font-size: 1.5rem;
      cursor: pointer;
      transition: color 0.3s;

      &:hover {
        color: color.adjust(v.$White, $lightness: -5%);
      }
    }
  }
}
