@use "global/variables" as v;
@use "global/mixins" as m;
/* Reset básico */

/* Sección principal */
.hero {
  text-align: center;
  padding: 80px 20px;
  background: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)),
    url("../img/1.jpg") center/cover no-repeat;
  color: #fff;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 20px;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7);
}

.hero p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto;
  text-shadow: 1px 1px 6px rgba(0, 0, 0, 0.6);
}

/* Sección de Industrias */
.industrias {
  padding: 60px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.industrias h2 {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 40px;
  color: #111;
}

.grid-industrias {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 30px;
}

.industria-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  padding: 30px;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.industria-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.industria-card img {
  height: 60px;
  margin-bottom: 20px;
}

.industria-card h3 {
  font-size: 1.5rem;
  margin-bottom: 15px;
}

.industria-card p {
  font-size: 1rem;
  color: #666;
}

/* Sección de calidad y precio */
.calidad {
  margin-top: 5rem;
  background-color: #111;
  color: #fff;
  text-align: center;
  padding: 60px 20px;
}

.calidad h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.calidad p {
  font-size: 1.2rem;
  max-width: 700px;
  margin: 0 auto;
  line-height: 1.6;
}

.Trabajos__Page {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-items: center;
  }

  @include m.desktopXL {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .proyectos__bottom--item__Page {
    .proyectos__bottom--item__contendio {
      display: flex;
      flex-direction: column;
      align-items: center;

      p {
        font-size: 2rem;
        font-weight: 200;
      }
    }
  }
}

/* //////////////////////////////////// */
.proyectos__bottom--item__Page {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  width: 35rem; // ancho fijo o puedes hacerlo responsive
  height: 30rem;

  .proyectos__bottom--item__contendio {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover; // asegura que la imagen llene el contenedor sin deformarse
      display: block;
      transition: transform 0.5s ease;
    }

    p {
      bottom: 67%;
      color: #fff;
      font-size: 2rem;
      font-weight: 200;
      left: 0rem;
      position: absolute;
      text-shadow: 0 0 8px rgba(0, 0, 0, 0.8);
      z-index: 2;
      padding: 1rem;
      background: #000;
      opacity: 0.7;
    }
  }

  /* Overlay */
  .overley__trabajos {
    position: absolute;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      to left,
      rgba(0, 0, 0, 0.8),
      rgba(0, 0, 0, 0.4)
    );
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: all 0.6s ease;
    z-index: 3;

    &--img img {
      width: 50px;
      height: 50px;
      margin-bottom: 10px;
      filter: invert(1);
    }

    &--texto {
      font-size: 1.2rem;
      text-align: center;
      padding: 0 10px;
    }
  }

  &:hover {
    .proyectos__bottom--item__contendio img {
      transform: scale(1.1);
    }

    .overley__trabajos {
      right: 0;
      opacity: 1;
    }
  }
}
